import React, { Component } from 'react';
import FullDataset from './Dataset/FullDataset';
import Chart2 from './Chart';

class App extends Component {

  constructor(props) {
    super(props);
    this.checkboxUsa = React.createRef();
    this.state = { fullDataSetObj: new FullDataset(), countryName: null, chart: null, DatanautsCount: 0, others: null };
  }

  componentDidMount() {
    this.showChart();
  }

  initialBiData() {
    return this.state.fullDataSetObj.getBiggest(this.state.fullDataSetObj.dataset, 10);
  }

  convertToChartData(biData) {
    if (biData.length === 0) return;

    const labels = [];
    const data = [];
    const backgroundColor = [];
    const colors = ['#FF6384', '#36A2EB', '#FFCE56'];
    let color = 0;
    const sortedArr = this.state.fullDataSetObj.sortByCount(biData);

    for (var i = 0; i < sortedArr.length; i++) {
      if (color === 3) color = 0;
      labels.push(sortedArr[i].name);
      data.push(sortedArr[i].count);
      backgroundColor.push(colors[color]);
      color++;
    }

    const chartData = {
      labels: labels,
      datasets: [{
        label: 'Members',
        data: data,
        backgroundColor: backgroundColor,
        hoverBackgroundColor: backgroundColor,
        borderColor: 'rgba(255,99,132,1)',
        borderWidth: 1,
        hoverBorderColor: 'rgba(255,99,132,1)',
      }]
    };

    this.setState({ chart: chartData });
  }

  showRestOfOthers(arr) {
    this.setState({ restOfOthers: arr });
  }

  showInitialChartState() {
    const arr = this.initialBiData();
    this.convertToChartData(arr);
    const others = arr[arr.length - 1];
    this.setState({ countryName: null, DatanautsCount: this.state.fullDataSetObj.getTotalCount(arr), others: others.list, restOfOthers: null });
  }

  showChart(label) {
    const fullDataSetObj = this.state.fullDataSetObj;
    if (label === 'USA') {
      const cities = fullDataSetObj.getCitiesByOwnerName('USA');
      const arr = fullDataSetObj.getBiggest(cities.arr, 10);
      this.convertToChartData(arr);
      const others = arr[arr.length - 1];
      this.setState({ countryName: label, DatanautsCount: fullDataSetObj.getTotalCount(arr), others: others.list, restOfOthers: null });
    } else
      if (label === 'Others') {
        let others = this.state.others;
        if (others.length > 24) {
          const arr = [];
          others = others.filter((e, index) => {
            if (index > 23) {
              arr.push(e);
            }
            return (index < 24);
          });
          this.showRestOfOthers(arr);
        } else
          this.setState({ restOfOthers: null });
        this.convertToChartData(others);
        this.setState({ countryName: '', DatanautsCount: fullDataSetObj.getTotalCount(others), others: null });
      } else
        if (!label || this.state.countryName) {
          this.showInitialChartState();
        } else {
          try {
            if (!fullDataSetObj.hasCountryOnlyUnknownCity(label)) {
              const cities = fullDataSetObj.getCitiesByOwnerName(label);
              const sorted = fullDataSetObj.sortByCount(cities.arr);
              this.convertToChartData(sorted);
              this.setState({ countryName: label, DatanautsCount: fullDataSetObj.getTotalCount(sorted), restOfOthers: null });
            }
          } catch (e) {
            this.showInitialChartState();
          }
        }
  }

  handleClick = (e, item) => {

    if (item[0]) {
      const idx = item[0]._index;
      const clickedLabel = this.state.chart.labels[idx];

      this.showChart(clickedLabel);
    }

  }

  render() {
    let others = '', othersCount=0;
    if(this.state.restOfOthers){    
      let arr = this.state.restOfOthers;
      arr.sort((a,b) => {
        return (b.name > a.name);
      })
      arr.map((e, index) => {
        othersCount += e.count;
      return (others += e.name.concat(' = ',e.count, ', '));
    })
  }

    const restOfOthers = !this.state.restOfOthers ?
      '' :
      <div>
        <p>Other datanauts: {othersCount}</p>
        <p>{others}</p>
      </div>
      ;

    return (
      <div className="App container">
        <header className="App-header">
          <h1 className="App-title">Searchnauts</h1>
          <p>NASA Datanauts around the World</p>
        </header>
        <h4>{'Chart with '+this.state.DatanautsCount+' Datanauts' }</h4>
        <h2>{this.state.countryName}</h2>
        <Chart2 chart={this.state.chart} onClick={this.handleClick} />
        {restOfOthers}
      </div>
    );
  }
}

export default App;



