import React from 'react';
import { HorizontalBar } from 'react-chartjs-2';

class Chart2 extends React.Component {
    render() {
        const options = {
            legend: {
                display: false
            },
            scales: {
                xAxes: [
                    {
                        scaleLabel: {
                            display: true,
                            labelString: 'Number of Members'
                        },
                        ticks: {
                            autoSkip: true,
                            min: 0
                        }
                    }
                ],

            },
            onClick: this.props.onClick
        };

        const chart = this.props && this.props.chart ?
            <HorizontalBar data={this.props.chart} ref='chart'
                width={50}
                height={25}
                options={options}
            />
            :
            '';

        return chart;
    }
}


export default Chart2;


